<template>
    <link rel='stylesheet'  href= "https://cdn.jsdelivr.net/npm/bulma@0.9.3/css/bulma.min.css"> 
   <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.4/css/all.min.css">
 
    <h1 style="padding: 50px;">桌次分配表</h1>
    <br>
   <div class="home">
     <div class="button-wrapper">
        
       <button v-for="(leader, index) in leaders" :key="index" :value="index" class="button name-size" @click="showDialog(index)">
         <div class="button-content">
            <p style="font-size: 16px; color: blue;">桌次{{ index+1 }}</p>
           <img src="./LOGO-40-FINAL-01.png" width="80" height="80">
           {{ leader }}
         </div>
       </button>
     </div>
 
     <!-- 对话框 -->
     <dialog id="infoModal">
       <div v-html="dialogContent" class="content"></div>
       <button id="close" @click="closeDialog">close</button>
     </dialog>
 
   </div>
 </template>
 
 <script>
 import 'bootstrap/dist/css/bootstrap.css';
 
 export default {
   name: 'SeatingView',
   data() {
     return {
       leaders: ['李柏妤', '黃雅茹', '許文明', '黃佩如', '林宜君', '許孟儒', '陳心禎', '鄭雅偉', '李沛揚', '林雅筑'],

       members: [["李柏妤", "陳彥安", "余彥翔", "吳宗星", "陳美郁","鄭國維", "王守學", "張嘉琪", "師書豪", "蔡蘭偉"],
                 ["黃雅茹", "韓怡君", "黃淑發", "黃麗星", "蔡慧君",    "林鳳珠", "張惠坤", "陳登沛", "黃馨玟", "陳琇茵"],
                 ["許文明", "黃健芸", "張耿亞", "陳韻伶", "趙宜佳",    "傅苑德", "溫依婷", "李美星", "胡嘉宏", "傅志嘉",],
                 ["黃佩如", "王文賢", "洪國怡", "李怡禎", "黃為谷",    "張玟君", "吳淑媛", "楊倍文", "汪美娟", "林于真"],
                 ["林宜君", "林敬治", "霍卓蘋", "吳元桓", "林宗利",    "黃鈺啟", "黃雅雯", "陳俊凱", "李怡如", "張佳怡"],
                  ["許孟儒", "林郁樺", "劉佳儀", "王孟修", "黃思如",    "李孟芬", "唐羿潔", "溫雅卿", "李威芝", "張曉邦"],
                  [ "陳心禎", "賴振芝", "黃建銘", "陳得利", "王雅瑄",    "張劭清", "夏宗憲", "許芝易", "林芳如", "張嘉貞"],
                  [ "鄭雅偉", "謝康綺", "彭家銘", "楊文君", "張沛玉",    "郭淑岑", "王亦姍", "鄧冰年", "劉景竹", "穆丹嬌"],
                  ["李沛揚", "趙偉銘", "陳政霖", "賴尚男", "林均昕",    "阮宏菱", "張怡靜", "賴丞映", "洪淑菱", "黃啟綠"],
                  ["李沛揚", "伍偉志", "王佳怡", "吳承佐", "柯嘉萍",    "陳立偉", "陳政音", "胡振謙", "黃琬婷", "王法豐"]
                 ],
       dialogContent: ''
     }
   },
   methods: {
     showDialog(index) {
       
       let members = this.members[index];
 
      
       let content = members.join('\n');
 
       
       this.dialogContent = content;
 
       
       let dialog = document.getElementById('infoModal');
       dialog.showModal();
     },
 
     closeDialog() {
       
       let dialog = document.getElementById('infoModal');
       dialog.close();
     }
   }
 }
 </script>
 
 <style scoped>
 .name-size {
   font-size: 30px;
 }
 .content {
   white-space: pre-line; 
 }
 
 .button-wrapper {
   display: flex;
   flex-wrap: wrap;
   justify-content: space-between; 
   margin: 0 auto; 
   max-width: 1000px; 
 }
 
 .button-content {
   display: flex;
   flex-direction: column;
   align-items: center;
 }
 
 .button-content img {
   margin-bottom: 5px; 
 }
 
 .button {
   width: calc(20% - 20px); 
   height: 120px; 
   margin-bottom: 20px; 
   background-color: #f0f0f0; 
   border: none; 
   border-radius: 10px; 
   cursor: pointer; 
 }
 
 @media (max-width: 768px) {
   .button-wrapper {
     flex-direction: column;
     align-items: center; 
   }
 
   .button {
     width: 100%;
   }
 }
 

 dialog {
   position:relative;
  
   border: 1px solid #ccc;
   background-color: #fff;
   padding: 20px;
   font-size: 20px; 
   width: 200px;
   /* height: 420px; */
 }
 
 
 dialog p {
   margin-bottom: 10px;
 }
 </style>
 