<template>
    <link href="https://cdn.jsdelivr.net/npm/bootstrap@5.3.3/dist/css/bootstrap.min.css" rel="stylesheet" integrity="sha384-QWTKZyjpPEjISv5WaRU9OFeRpok6YctnYmDr5pNlyT2bRjXh0JMhjY6hW+ALEwIH" crossorigin="anonymous">
    <link rel="stylesheet" href="https://fonts.googleapis.com/css2?family=Material+Symbols+Outlined:opsz,wght,FILL,GRAD@20..48,100..700,0..1,-50..200" />
  
    <div>
      <h3 style="padding-top: 30px; font-size: 50px;">捐款芳名錄</h3>
  
      <table class="table">
        <tbody>
          <tr v-for="(chunk, index) in chunkedUsers" :key="index">
            <td style="text-align: center;" v-for="(user, i) in chunk" :key="i">{{ user }}</td>
          </tr>
        </tbody>
      </table>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        users: [
            "林詩越", "胡舒蕊", "康培聰", "汪汝露", "黃清霄", "邱威家", "盧德霄", "吳勳可", "游歆華", "嚴柏樹",
            "洪潮鼎", "張晨琉", "洪念泛", "黃勳業", "鄭娟莎", "侯熙順", "李晉黛", "郭丞貴", "杜義道", "蕭彰港",
            "林永秋", "謝川棋", "姜宇年", "梁弘賢", "薛廷運", "劉淇穎", "張展彥", "童益誠", "沈嘉晴", "古世倫",
            "陳彬淳", "陳儀進", "鄒柏進", "張宛恩", "王泓煥", "施祥雙", "李卓苑", "潘祥苑", "彭濱青", "劉爍進",
            "溫星茜", "洪笑蓁", "廖嶽越", "楊凱眾", "陳依雨", "馬才流", "王秉興", "高志賢", "陳禎臻", "周先茹",
            "連研飄", "巫永知", "王庭謹", "吳易遠", "黃昭淑", "王丞聰", "張朋熙", "呂卓品", "汪勛梓", "林木禹",
            "張新鳳", "郭南璐", "游俞理", "呂善圖", "林慧榕", "陳冠臻", "林宥嵐", "陳詠馨", "陳婉盈", "林嘉豪",
            "許捷蓁", "施春術", "黃卓諺", "鍾泓運", "傅潮融", "劉偲綺", "吳易眉", "林澍萬", "翁瑛鳳", "陸敬清",
            "莊慈瑋", "顏影恬", "陳召安", "黃子晨", "江先念", "張巖温", "何冉珺", "郭又濮", "周海温", "李中珈",
            "賴興貴", "王康淇", "林名韶", "洪潔芪", "羅中庭", "方永石", "歐晟暢", "尤常斯", "徐樺淳", "游方賢"
        ],
      };
    },
    computed: {
      chunkedUsers() {
        const chunkSize = 3;
        return this.users.reduce((resultArray, item, index) => {
          const chunkIndex = Math.floor(index / chunkSize);
  
          if (!resultArray[chunkIndex]) {
            resultArray[chunkIndex] = []; // 開始一個新的 chunk
          }
  
          resultArray[chunkIndex].push(item);
          return resultArray;
        }, []);
      },
    },
  };
  </script>
  
  <style scoped>
  

  table {
    /* margin-left: -100px; */
    text-align: center;
    
  }
  

  </style>
  